.mtt {
    height: 50vw;
    writing-mode: vertical-rl;
    text-orientation: mixed;
    font-size: 5vw;
}


/* This section configures the horizonal scrolling*/
.wrapper {
    display: flex;
    flex-direction: row;
    width: 304vw;
    transform: rotate(90deg) translateY(-100vh);
    transform-origin: top left;
}

.outer-wrapper {
    width: 100vh;
    height: 100vw;
    transform: rotate(-90deg) translateX(-100vh);
    transform-origin: top left;
    overflow-y: scroll;
    overflow-x: hidden;
    position: absolute;
    scrollbar-width: none;
    -ms-overflow-style: none;
}

::-webkit-scrollbar {
    display: none;
}

/* Horizontal Scroll: slides config */
.slide {
    width: 100vw;
    height: 100vh;
}

